import React, {useState,useEffect} from 'react';
import './App.css';
import foto from './minhaFoto.jpg'
import Email from "./assets/email.svg";
import Telefone from "./assets/telefone.svg";
import Linkedin from "./assets/linkedin.svg";

function App() {
  const [textoDinamico,setTextoDinamico] = useState('texto 1');
  const desenvolvimentoString = 'Meu';
  const eletronicaString = 'site';
  const experienciaString = 'ta';
  const formacaoString = 'ficando';
  const outrosString = 'massa';

  const [botoes,setBotoes] = useState([true,true,true,true,true]);

  function getWindowDimensions () {
    const {innerWidth: width, innerHeight: height} = window;
    return {
      width,
      height
    };
  }

  function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

    useEffect(() => {
      function handleResize() {
        setWindowDimensions(getWindowDimensions());
      }

      window.addEventListener('resize', handleResize);
      return () => window.removeEventListener('resize', handleResize);
    }, []);

    return windowDimensions;
  }

  const {width,height} = useWindowDimensions();
  console.log("width: " + width);
  console.log("height: " + height);

  return (
    <div className="App">
      <body>
        <div id="main">

          <div id="header">

            {/*<div id="caixa_curriculo">
              <h1 id="curriculo">CURRÍCULO</h1>
  </div>*/}

            <div id="caixa_com_nome">
              <h1 id="title" >Fernando José Martins Heusi da Silva</h1>	
            </div>

            <div id="contatos">
              <div  class="contato">
                <img src={Telefone} class="icone" alt="telefone"/><h2 class="contato_texto"> (21)99630-0109: </h2>
              </div>

              <div  class="contato">
                {/*<Email/><h2 class="contato_texto" >fernandoj.mhs@gmail.com</h2>*/}
                <h2 class="contato_texto" ><img src={Email} class="icone" alt="email"/>fernandoj.mhs@gmail.com</h2>
              </div>

              <div  class="contato">
                <a href="https://www.linkedin.com/in/fernandoheusi/" target="_blank"> <h2 class="contato_texto">  <img src={Linkedin} class="icone" alt="Linkedin"/>linkedin.com/in/fernandoheusi</h2></a>
              </div>
            </div>

          </div>

          <div id="content">
            <div id="Perfil">
              <div id="textoInicial">
                <h1 class="titulo">Lorem Ipsum</h1>
                <p class="texto">Bem vindo ao meu site! Meu nome é Fernando José, nesses ultimos anos tenho desenvolvido muitas coisas interessantes na area da Tecnologia, com maior enfase em programaçãp</p>
              </div>
              <img id="minhaFoto" src={foto} alt="minhafoto" />
            </div>

            <div id="texto_botoes">
              <div id="botoes">
                <div type="button" onClick={()=>setTextoDinamico(desenvolvimentoString)} class="caixa_com_titulo">
                  <h2>DESENVOLVIMENTO WEB</h2>
                </div>
                <div type="button" onClick={()=>setTextoDinamico(eletronicaString)} class="caixa_com_titulo">
                  <h2>Eletrônica</h2>
                </div>
                <div type="button" onClick={()=>setTextoDinamico(experienciaString)} class="caixa_com_titulo">
                  <h2>Experiencia</h2>
                </div>
                <div type="button" onClick={()=>setTextoDinamico(formacaoString)} class="caixa_com_titulo">
                  <h2>Formação</h2>
                </div>
                <div type="button" onClick={()=>setTextoDinamico(outrosString)} class="caixa_com_titulo">
                  <h2>Outros</h2>
                </div>
              </div>
              {width > 935 ? <div id="texto_dinamico" ><p style={{"font-size": "45px"}} class="texto">{textoDinamico}</p></div> : null}
            </div>
          </div>
          <div class="menu"></div>
          <div class="footer"></div>
        </div>
      </body>
    </div>
  );
}

export default App;
